import { isMobile } from "../../shared/helpers/viewport";
import { useViewportWidth } from "../../shared/hooks/viewport";
import { Button } from "../Button/Button";
import { FEATURES } from "./Main.consts";
import "./Main.scss";

export function Main() {
  const isMobileDevice = useViewportWidth(isMobile, false);
  return (
    <div className="contentContainer">
      <div className="block">
        <div className="leftBlock">
          <p className="title">
            
            <br />
            
          </p>
          <p className="markedTitle">WELBEX</p>
          <span>   </span>
        </div>

        <div className="rightBlock">
          <p className="title">
             
            <span className="title_marked"> </span>{" "}
            :
          </p>
          <div className="features_block">
            {FEATURES.map((feature) => {
              return isMobileDevice ? (
                <div className="feature_mobile">
                  <span className="gradient"> </span>
                  {feature.title}
                </div>
              ) : (
                <div className="features">
                  <p>{feature.title}</p>
                  <span>{feature.subtitle}</span>
                </div>
              );
            })}
          </div>
          <Button
            text=" "
            type="primary"
            width={262}
            height={61}
          />
        </div>
      </div>
    </div>
  );
}
